.init-content {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
}

.init-background {
    position: fixed;
    top: 53px;
    left: 0;
    width: 100vw;
    height: 140px;
    background: -webkit-gradient( linear, left bottom, left top, from(white), color-stop(30%, rgba(255,255,255,0.0))), url(/images/init.jpg);
    background: -webkit-linear-gradient( bottom, white 0%, rgba(255,255,255,0.0) 30%), url(/images/init.jpg);
    background: -o-linear-gradient( bottom, white 0%, rgba(255,255,255,0.0) 30%), url(/images/init.jpg);
    background: linear-gradient( 0deg, white 0%, rgba(255,255,255,0.0) 30%), url(/images/init.jpg); ;
    background-size: cover;
    background-position: center center;
}

.init-content .spinner {
    position: fixed;
    bottom: 50px;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}


