.whitespace-col {
    height: 63px;
}

.card-content-narrow {
    padding-top: 8px !important;
    padding-bottom: 2px !important;
}

.card-content-top-separator {
    border-top: 1px solid rgba(160, 160, 160, 0.2);
}

.card-content-no-padding-top {
    padding-top: 0 !important;
}

nav {
    position: relative;
    top: 0;
    left: 0;
    z-index: 2;
}
.navigation {
    position: fixed;
}

.logo-link {
    margin-left: 10px;
}

.logo {
    height: 55px;
}

.card-content p {
    white-space: pre-line;
}

.news-date {
    margin-bottom: 12px !important;
    border-bottom: 1px solid rgba(160, 160, 160, 0.2);
}

.page-footer {
    margin-top: 40px;
}

.no-margin-bottom {
    margin-bottom: 0;
}

.footer-settings-link {
    position: relative;
    top: 15px;
    left: -9px;
}

.footer-settings-link i {
    font-size: 30px !important;
}

.vpadding-extra {
    padding-top: 5px;
    padding-bottom: 5px;
}

.page-footer {
    padding-top: 0 !important;
}

.page-content {
    min-height: calc(100vh - 99px);
}



