.login-content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 150vh;
}

.login-background {
    position: absolute;
    top: 53px;
    left: 0;
    width: 100vw;
    height: 140px;
    background: -webkit-gradient( linear, left bottom, left top, from(white), color-stop(30%, rgba(255,255,255,0.0))), url(/images/login.jpg);
    background: -webkit-linear-gradient( bottom, white 0%, rgba(255,255,255,0.0) 30%), url(/images/login.jpg);
    background: -o-linear-gradient( bottom, white 0%, rgba(255,255,255,0.0) 30%), url(/images/login.jpg);
    background: linear-gradient( 0deg, white 0%, rgba(255,255,255,0.0) 30%), url(/images/login.jpg); ;
    background-size: cover;
    background-position: center center;
}

.login-content .whitespace-col {
    height: 157px !important;
}

.input-field input.valid {
    color: #0288d1 !important;
    border-color: #0288d1 !important;
    -webkit-box-shadow: 0 0px 0 0 #0288d1 !important;
            box-shadow: 0 0px 0 0 #0288d1 !important;
}

.input-field label.valid, .input-field label.active {
    color: #0288d1 !important;
}

textarea {
    border-color: #0288d1 !important;
    -webkit-box-shadow: 0 0px 0 0 #0288d1 !important;
            box-shadow: 0 0px 0 0 #0288d1 !important;
}


.login-footer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.login-footer a {
    padding: 10px 20px;
}


